import { createWebHashHistory, createRouter } from "vue-router";
import List from './components/PostList.vue'
import Main from './components/MainPage.vue'
import Detail from './components/DetailPage.vue'
import Author from './components/AuthorPage.vue'
import Comments from './components/CommentsPage.vue'

const routes = [
  {
    path: "/list",
    component: List,
  },
  {
    path: "/detail/:id(\\d+)",
    component: Detail,
    children:[
      {
        path:"author",
        component:Author,
      },
      {
        path:"comment",
        component: Comments,
      }
    ]
  },
  {
    path: "/",
    component:Main,
  },
  {
    path:'/:anythine(.*)*',
    component:Main,
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router; 